import "core-js/modules/es.array.push.js";
// 使用vue3 使用vuex的方式
import { useStore } from 'vuex';
import { useRouter } from 'vue-router';
import { showToast } from 'vant';
export default {
  props: ['storeData'],
  setup(props) {
    let store = useStore();
    let router = useRouter();

    // 处理加入购物车点击事件
    // 点击了之后，就把 storeData 里面num大于0的商品筛选出来
    const handleAddCart = type => {
      const newList = store.state.cartList || [];
      props.storeData.forEach(item => {
        item.data.items?.forEach(item => {
          item.children.forEach(item => {
            if (item.num > 0) {
              newList.push(item);
            }
          });
        });
      });
      if (newList.length === 0) {
        showToast('请选购商品！');
        return;
      }
      store.commit('addCart', newList);
      type === 'buy' ? goCart() : "";
    };

    // 处理购物车点击事件
    const goCart = () => {
      router.push('/cart');
    };

    // 处理立即购买
    const goBuy = () => {
      handleAddCart('buy');
    };
    return {
      handleAddCart,
      store,
      goCart,
      goBuy
    };
  }
};