import "core-js/modules/es.array.push.js";
import { reactive, toRefs } from 'vue';
import FoodListItem from '../../../components/FoodListItem.vue';
export default {
  components: {
    FoodListItem
  },
  props: ['index', 'foodData'],
  setup(props) {
    let data = reactive({
      activeIndex: 0,
      items: [{
        text: '分组 1'
      }, {
        text: '分组 2'
      }],
      subItem: []
    });

    // 初始化左侧导航数据
    const initData = () => {
      let newArray = [];
      props.foodData?.items?.forEach((item, index) => {
        newArray.push({
          text: item.text
        });
        if (data.activeIndex === index) {
          data.subItem = item.children;
        }
      });
      data.items = newArray;
    };
    initData();

    // 点击左侧导航
    const navClick = i => {
      data.activeIndex = i;
      initData();
    };

    // 处理红色加号
    const handleAdd = id => {
      data.subItem.forEach((item, index) => {
        if (id === item.id) {
          item.add = false;
          item.num += 1;
        }
      });
      console.log(data.subItem);
    };

    // 处理步进器变化
    const handleChange = (value, detail) => {
      data.subItem.forEach((item, index) => {
        if (item.id === detail.name) {
          item.num = value;
        }
      });
      console.log(data.subItem);
    };
    return {
      ...toRefs(data),
      navClick,
      handleAdd,
      handleChange
    };
  }
};